"use client";

// React and Next
import Link from "next/link";
import { useRef, useState } from "react";

// Utils
import { sortCategories } from "@/utilities/globalFunctions";
import { Cookiescategory } from "@/utilities/cookies";

//Redux
import { useDispatch } from "react-redux";
import {
  breadcrumallcategory,
  breadcrumb,
  categoryname,
  productslug,
  subproduct,
} from "@/redux/slices/userSlice";

export default function TopCategory({ topCategories }) {
  const dispatch = useDispatch();
  const sortedTopCategories = sortCategories(topCategories);

  const containerRef = useRef(null);
  const [underlineStyle, setUnderlineStyle] = useState({ left: 0, width: 0 });
  const [subCategoryData, setSubCategoryData] = useState();

  const handleHover = (e, data) => {
    setSubCategoryData(data);
    const link = e.currentTarget;
    const container = containerRef.current;
    const linkRect = link.getBoundingClientRect();
    const containerRect = container.getBoundingClientRect();

    const left = linkRect.left - containerRect.left + container.scrollLeft; // â† this fixes it
    const width = link.offsetWidth;

    setUnderlineStyle({ left, width });
  };

  const handleCategoryNames = (name, slug) => {
    dispatch(categoryname(name));
    dispatch(breadcrumb(false));
    dispatch(productslug(slug));
    dispatch(breadcrumallcategory(false));
    dispatch(subproduct(false));
    Cookiescategory(name);
  };
  const scrollVelocityRef = useRef(0);
  const animationFrameRef = useRef(null);

  const smoothScroll = () => {
    const container = containerRef.current;
    if (!container) return;

    container.scrollLeft += scrollVelocityRef.current;
    scrollVelocityRef.current *= 0.85; // friction

    if (Math.abs(scrollVelocityRef.current) > 0.5) {
      animationFrameRef.current = requestAnimationFrame(smoothScroll);
    }
  };

  const handleWheel = (e) => {
    const container = containerRef.current;
    if (!container) return;

    // momentum scrolling
    scrollVelocityRef.current += e.deltaY * 0.3;

    cancelAnimationFrame(animationFrameRef.current);
    animationFrameRef.current = requestAnimationFrame(smoothScroll);
  };

  return (
    <div className="sticky top-[81px] z-50 hidden border-b border-solid border-[#EEEFF1] bg-white lg:block">
      <div className="group relative mx-auto max-w-screen-2xl px-4">
        <div
          ref={containerRef}
          className="scrollbar-hidden group relative flex max-h-[49px] gap-x-4 overflow-auto"
          onWheel={handleWheel}
          style={{ overscrollBehavior: "contain" }} // stops body scroll on wheel
        >
          {sortedTopCategories?.map((data, index) => (
            <Link
              prefetch={false}
              key={index}
              href={`/product/${data?.categorySlug}`}
              className="relative flex items-center space-x-2 whitespace-nowrap py-4 text-sm font-normal capitalize leading-4 tracking-wider text-[#333333] transition-all hover:font-[500] hover:text-[#047600] dark:text-white"
              onClick={() =>
                handleCategoryNames(data?.name, data?.categorySlug)
              }
              onMouseEnter={(e) => handleHover(e, data)}
            >
              {data?.categoryNameTrans ? data?.categoryNameTrans : data?.name}
            </Link>
          ))}
          {/* Moving underline */}
          <span
            className="absolute bottom-0 !ml-0 hidden h-[3px] bg-[#047600] transition-all duration-300 group-hover:block"
            style={{
              left: underlineStyle.left,
              width: underlineStyle.width,
            }}
          />
        </div>

        <div className="invisible absolute left-0 top-full z-50 mt-[1px] max-h-[60vh] w-full bg-transparent px-4 opacity-0 transition-all duration-200 group-hover:visible group-hover:opacity-100">
          <div className="mx-auto max-w-screen-2xl overflow-auto bg-white shadow-[0px_2px_4px_#00000014]">
            <div className="flex bg-white">
              {subCategoryData &&
                subCategoryData?.children &&
                subCategoryData?.children?.map((subCategory, index) => (
                  <div
                    key={index}
                    className="w-max min-w-[150px] bg-white p-[24px] [&:nth-child(even)]:bg-[#F9F9FC]"
                  >
                    <h3 className="mb-[24px] text-sm font-semibold leading-[17px] text-[#047600] hover:underline">
                      <Link
                        className="capitalize"
                        prefetch={false}
                        href={`/product/${subCategory?.categorySlug}`}
                        onClick={() =>
                          handleCategoryNames(
                            subCategory?.name,
                            subCategory?.categorySlug,
                          )
                        }
                      >
                        {subCategory?.categoryNameTrans
                          ? subCategory?.categoryNameTrans
                              .charAt(0)
                              .toUpperCase() +
                            subCategory?.categoryNameTrans
                              .slice(1)
                              .toLowerCase()
                          : subCategory?.name.charAt(0).toUpperCase() +
                            subCategory?.name.slice(1).toLowerCase()}
                      </Link>
                    </h3>
                    <div className="block">
                      {subCategory &&
                        subCategory?.children &&
                        subCategory?.children?.map(
                          (subCategoryChild, index) => (
                            <Link
                              prefetch={false}
                              key={index}
                              href={`/product/${subCategoryChild?.categorySlug}`}
                              onClick={() =>
                                handleCategoryNames(
                                  subCategoryChild?.name,
                                  subCategoryChild?.categorySlug,
                                )
                              }
                              className="mb-[12px] mt-0 block text-[14px] font-normal capitalize leading-[17px] text-[#333333] last-of-type:mb-0 hover:underline"
                            >
                              {subCategoryChild?.categoryNameTrans
                                ? subCategoryChild?.categoryNameTrans
                                    .charAt(0)
                                    .toUpperCase() +
                                  subCategoryChild?.categoryNameTrans
                                    .slice(1)
                                    .toLowerCase()
                                : subCategoryChild?.name
                                    .charAt(0)
                                    .toUpperCase() +
                                  subCategoryChild?.name.slice(1).toLowerCase()}
                            </Link>
                          ),
                        )}
                    </div>
                  </div>
                ))}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
