"use client";

// React and Next
import { useEffect, useState } from "react";

// Utils
import { imageUrl } from "@/utilities/url";

export default function Banner({ bannerList }) {
  const [currentIndex, setCurrentIndex] = useState(0);
  const [filterData, setFilterData] = useState();

  const bannervalidatinon = (link) => {
    if (link !== "") {
      let url = "";
      if (!/^http[s]?:\/\//.test(link)) {
        url += "http://";
      }
      url += link;
      window.open(url);
    }
  };

  useEffect(() => {
    const carouselItems = document.querySelectorAll("#carousel-item");
    carouselItems.forEach((item, index) => {
      item.classList.remove("hidden");
      if (index !== currentIndex) {
        item.classList.add("hidden");
      }
    });
  }, [currentIndex]);

  useEffect(() => {
    const intervalId = setInterval(() => {
      setCurrentIndex((currentIndex + 1) % bannerList?.length);
    }, 3000); // 5 seconds

    return () => clearInterval(intervalId);
  }, [currentIndex, bannerList]);

  const handleIndicatorClick = (index) => {
    setCurrentIndex(index);
  };

  // new code
  useEffect(() => {
    if (bannerList) {
      let newData = [...bannerList];

      if (currentIndex >= bannerList?.length - 3) {
        const filterLength = bannerList?.length - currentIndex;
        const addData = 3 - (filterLength - 1);
        const sliceData = newData?.slice(currentIndex + 1);
        const filterSlice = bannerList?.filter(
          (sdata, index) => index < addData,
        );
        setFilterData(sliceData.concat(filterSlice));
      } else {
        setFilterData(newData?.slice(currentIndex + 1, currentIndex + 4));
      }
    }
  }, [currentIndex]);

  return (
    <div className="mx-auto grid max-w-screen-2xl gap-4 p-4 lg:p-4">
      <div className="col-span-8 row-span-2 aspect-auto h-auto max-[1920px]:h-[480px] max-[1440px]:h-[488px] max-[1380px]:h-[525px] max-[1200px]:h-[556px] max-[1023px]:h-[525px] max-[900px]:h-[500px] max-[760px]:h-[360px] max-[500px]:h-[250px] min-[1920px]:h-[480px]">
        <div id="carousel-example" className="relative w-full">
          {/* Carousel wrapper */}
          <div className="relative aspect-auto overflow-hidden max-[1920px]:h-[480px] max-[1440px]:h-[488px] max-[1380px]:h-[525px] max-[1200px]:h-[556px] max-[1023px]:h-[525px] max-[900px]:h-[500px] max-[760px]:h-[360px] max-[500px]:h-[250px] min-[1920px]:h-[480px]">
            {bannerList?.map((data, index) => (
              <div
                key={data?.bannerId}
                id={`carousel-item-${data?.bannerId}`}
                className={`duration-700 ease-in-out ${
                  currentIndex === index ? "active" : ""
                } ${
                  currentIndex > index
                    ? "animate-from-left"
                    : " animate-from-right"
                }`}
                onClick={(e) => bannervalidatinon(data?.link)}
              >
                <img
                  src={
                    imageUrl +
                    "?path=" +
                    data?.bannerImages?.[0]?.imagePath +
                    "&name=" +
                    data?.bannerImages?.[0]?.imageName +
                    "&width=1264&height=455"
                  }
                  width={1264}
                  height={455}
                  // objectFit="contain"
                  layout="responsive"
                  alt={data?.title}
                  onError={({ currentTarget }) => {
                    currentTarget.onerror = null;
                    currentTarget.src = "/img/no-image.png";
                    currentTarget.alt = "no-image";
                  }}
                  className={`aspect-122-9 absolute left-0 top-0 block w-full max-[1920px]:h-[480px] max-[1440px]:h-[488px] max-[1380px]:h-[525px] max-[1200px]:h-[556px] max-[1023px]:h-[525px] max-[900px]:h-[500px] max-[760px]:h-[360px] max-[500px]:h-[250px] min-[1920px]:h-[480px] ${
                    data?.link != "" && "cursor-pointer"
                  }`}
                />
              </div>
            ))}
          </div>
          {/* Slider indicators */}
          <div className="absolute bottom-5 left-1/2 z-30 flex -translate-x-1/2 space-x-4 rtl:space-x-[unset] rtl:space-x-reverse">
            {bannerList?.map((item, index) => (
              <button
                key={index}
                id={`carousel-indicator-${item?.bannerId}`}
                type="button"
                className={`h-[10px] w-[10px] rounded-full rtl:ml-4 ${
                  currentIndex === index
                    ? "border border-white bg-black"
                    : "bg-gray-300"
                }`}
                aria-current={currentIndex === index ? "true" : "false"}
                aria-label={`Slide ${item?.bannerId}`}
                onClick={() => handleIndicatorClick(index)}
              />
            ))}
          </div>
        </div>
      </div>

      {bannerList?.length > 3 && (
        <div className="col-span-8 hidden grid-cols-3 gap-x-1 lg:grid lg:gap-x-5">
          {filterData != undefined &&
            filterData?.map((data, index) => (
              <div
                key={index}
                className="h-auto]"
                onClick={(e) => bannervalidatinon(data?.link)}
              >
                <img
                  src={
                    imageUrl +
                    "?path=" +
                    data?.bannerImages?.[0]?.imagePath +
                    "&name=" +
                    data?.bannerImages?.[0]?.imageName +
                    "&width=408&height=220"
                  }
                  width={408}
                  height={220}
                  // objectFit="contain"
                  layout="responsive"
                  alt={data?.title}
                  onError={({ currentTarget }) => {
                    currentTarget.onerror = null;
                    currentTarget.src = "/img/no-image.png";
                    currentTarget.alt = "no-image";
                  }}
                  className={`aspect-video h-full w-full md:object-fill ${
                    data?.link != "" && "cursor-pointer"
                  }`}
                />{" "}
              </div>
            ))}
        </div>
      )}
    </div>
  );
}
